$m-layer-close-name: 'm-layer-close';
$m-layer-close-x: 54px;
$m-layer-close-y: 0px;
$m-layer-close-offset-x: -54px;
$m-layer-close-offset-y: 0px;
$m-layer-close-width: 17px;
$m-layer-close-height: 17px;
$m-layer-close-total-width: 142px;
$m-layer-close-total-height: 44px;
$m-layer-close-image: '../images/icons/mobile-icons.png';
$m-layer-close: (54px, 0px, -54px, 0px, 17px, 17px, 142px, 44px, '../images/icons/mobile-icons.png', 'm-layer-close', );
$m-login-name: 'm-login';
$m-login-x: 0px;
$m-login-y: 0px;
$m-login-offset-x: 0px;
$m-login-offset-y: 0px;
$m-login-width: 17px;
$m-login-height: 22px;
$m-login-total-width: 142px;
$m-login-total-height: 44px;
$m-login-image: '../images/icons/mobile-icons.png';
$m-login: (0px, 0px, 0px, 0px, 17px, 22px, 142px, 44px, '../images/icons/mobile-icons.png', 'm-login', );
$m-logout-name: 'm-logout';
$m-logout-x: 27px;
$m-logout-y: 0px;
$m-logout-offset-x: -27px;
$m-logout-offset-y: 0px;
$m-logout-width: 17px;
$m-logout-height: 21.5px;
$m-logout-total-width: 142px;
$m-logout-total-height: 44px;
$m-logout-image: '../images/icons/mobile-icons.png';
$m-logout: (27px, 0px, -27px, 0px, 17px, 21.5px, 142px, 44px, '../images/icons/mobile-icons.png', 'm-logout', );
$spritesheet-width: 142px;
$spritesheet-height: 44px;
$spritesheet-image: '../images/icons/mobile-icons.png';
$spritesheet-sprites: ($m-layer-close, $m-login, $m-logout, );
$spritesheet: (142px, 44px, '../images/icons/mobile-icons.png', $spritesheet-sprites, );


@mixin m-sprite-width($sprite) {
  width: nth($sprite, 5);
}

@mixin m-sprite-height($sprite) {
  height: nth($sprite, 6);
}

@mixin m-sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 3);
  $sprite-offset-y: nth($sprite, 4);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin m-sprite-size() {
  background-size: ($spritesheet-width / 2) auto;
}

@mixin m-sprite-image($sprite) {
  $sprite-image: nth($sprite, 9);
  background-image: url(#{$sprite-image});
}

@mixin m-sprite($sprite) {
  @include m-sprite-image($sprite);
  @include m-sprite-size();
  @include m-sprite-position($sprite);
  @include m-sprite-width($sprite);
  @include m-sprite-height($sprite);
}


@mixin m-sprites($m-sprites) {
  @each $sprite in $m-sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include m-sprite($sprite-);
    }
  }
}
